$white: #fff;

body {
  background: url('../images/background.jpg') no-repeat;
  background-size: cover;
  background-color: red;
  display: grid;
  user-select: none;
  margin: 10px 20px 10px 20px;
}

h1 {
  color: #ddd;

  @media screen and (max-width: 600px) {
    display: none;
  }
}

p {
  color: white;

  @media screen and (max-width: 600px) {
    display: none;
  }
}
